.register-block {

  @include onMobile {
    margin: 70px 16px;
    ;
  }

  @include onDesktop {
    margin: 165px 213px;
  }

  &--is-hidden {
    display: none;
  }
}

;

.selector-form {
  padding: 28px 16px 20px 16px;
  background-color: #fff;
  border-radius: 16px;
  display: flex;
  flex-direction: column;

  &--is-hidden {
    display: none;
  }

  &__close-form {
    width: 20px;
    height: 20px;
    align-self: flex-end;
    cursor: pointer;
    transition: all 0.3s;
    margin-bottom: 5px;

    &:hover {
      scale: 1.03;
      transform-origin: center;
    }
  }

  &__title {
    margin-top: 23px;
    font-weight: 500;
    font-size: 20px;
    line-height: 140%;
    color: #000;
    margin-bottom: 24px;
  }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  &__item {
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: row;
    cursor: pointer;
    margin-bottom: 16px;

    &--color-picker {
      cursor: pointer;
      width: 32px;
      height: 32px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 2px solid #d9d9d9;
      background-color: #d9d9d9;

      &--inner {
        display: none;
        position: absolute;
        left: 9.5px;
        top: 8px;
        width: 22px;
        height: 22px;
        border-radius: 33px;
        background-color: #d9d9d9;

        @include onDesktop {
          & {
            left: 10.5px;
          }
        }
      }

      &:checked ~ &--inner {
        display: block;
      }
    }

    &--color-text {
      font-weight: 300;
      font-size: 16px;
      line-height: 130%;
      color: #000;
      margin-left: 14px;
    }
  }
}

;

.form-buttons {
  margin-top: 240px;
  display: flex;
  justify-content: space-between;
}

.button {
  width: auto;
  height: 50px;
  background: #d9d9d9;
  border-radius: 6px;
  transition: all 0.3s;
  border: none;
  background-color: none;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 17px;
  cursor: pointer;

  &:hover {
    scale: 1.01;
  }

  &__prev-button {
    background-color: #d9d9d9;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: rgba(0, 0, 0, 0.6);
  }

  &__next-button {
    background-color: $c-yellow;
    font-weight: 700;
    font-size: 18px;
    line-height: 130%;
    color: #000;
  }
}

.register {
  &__list {
    list-style-type: none;
    padding: 0;
  }

  &__item {
    position: relative;
    margin-bottom: 32px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__input {
    height: 46px;
    width: 100%;
    border: none;
    outline: none;
    border-bottom: 1px solid #d9d9d9;

    &--is-error {
      border-bottom: 1px solid #f30;
    }

    &--placeholder {
      left: 24px;
      position: absolute;
      font-weight: 300;
      font-size: 16px;
      line-height: 140%;
      color: #000;

      &--is-error {
        color: #f30;
        border-color: #f30;
      }

      &--number-main {
        left: 24px;
        font-weight: 400;
        font-size: 20px;
        line-height: 140%;

        &--is-error {
          color: #f30;
          border-color: #f30;
        }
      }

      &--number {
        left: 82px;
        top: 2px;
      }
    }

    &:focus~&--placeholder,
    &:valid~&--placeholder {
      display: none;
    }
  }
}