.header {

  &--is-hidden {
    display: none;
  }
  position: relative;

  padding: 164px 16px 206px 16px;
  display: flex;
  justify-content: flex-start;

  @include onDesktop {
    padding: 144px 150px;
  }

  &__info {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 140%;

    &--yellow {
      color: $c-yellow;
    }
  }

  &__subtitle {
    margin-top: 20px;
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
  }

  &__bottom {
    margin-top: 20px;
  }

  &__register-button {
    margin-top: 32px;
    transition: all 0.3s;
    cursor: pointer;

    &:hover {
      box-shadow: 0px 0px 60px -20px rgba(248,250,127,1);
    }
  }
}

.bottom {
  display: flex;
  flex-direction: column;
  font-weight: 400;
  font-size: 16px;
  color: $c-gray;

  &__title {
    margin-bottom: 12px;
    line-height: 130%;
  }

  &__list {
    display: flex;
    gap: 11px;
    flex-direction: column;
    margin: 0;
  }
}

.register-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 216px;
  height: 52px;
  background: $c-yellow;
  border-radius: 16px;
  border: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 160%;
  color: #000;
};
