.main-info {
  background-color: #000;
  display: flex;
  justify-content: center;

  &--is-hidden {
    display: none;
  }

  @include onMobile {
    padding-top: 60px;
    flex-direction: column;
  }

  @include onDesktop {
    padding-top: 57px;
    flex-direction: row;
    
  }

  &__box {
    display: flex;
    flex-direction: column;
    justify-content: center;

    @include onDesktop {
      margin-right: 70px;
    }
  }
}

.box {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include onDesktop {
    margin-bottom: 76px;
  }

  &:last-child {
    @include onMobile {
      margin-bottom: 130px;
    }
    @include onDesktop {
      margin-bottom: 76px;
    }
  }

  &__images {
    width: 142px;
    height: 149px;
    margin-top: 60px;
    margin-bottom: 18px;
  }

  &__list {
    position: relative;
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: row;
    

    &--line {
      display: block;
      width: 2px;
      background-color: $c-yellow;
      border: 1px solid $c-yellow;
      border-radius: 15px;
      margin-right: 24px;
    }
  }

  &__item {
    max-width: 276px;
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: #fff;
    margin-bottom: 12px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}